@tailwind utilities;

@layer utilities {
  .overflow-wrap-anywhere {
    overflow-wrap: anywhere;
    word-break: break-word;
    hyphens: auto;
  }
}

.font-mono {
  font-variant-ligatures: none;
}

.scrollbar-none {
  scrollbar-width: none;
  &::-webkit-scrollbar {
    display: none !important;
  }
}

.bg-checkered {
  background-image: url("data:image/svg+xml,%0A%3Csvg xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill='%23F0F0F0' d='M0 0h8v8H0zm8 8h8v8H8z'/%3E%3C/svg%3E");
  background-size: 16px 16px;
}

.bg-response-body-auto {
  @apply bg-gray-100/50 dark:bg-white/[0.03];
}

.bg-response-body-dark {
  @apply bg-white/[0.03];
}

.border-standard {
  @apply border border-gray-200/70 dark:border-white/10;
}

.text-playground-input {
  @apply border-standard rounded-lg text-sm px-2.5 py-1.5 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-white/20 focus:border-gray-300 dark:focus:border-white/20;
}

.code-highlight {
  border-radius: 0.25rem;
  padding: 0.125rem 0.1875rem;
  margin: 0 -0.1875rem;
}

.dragging-ew,
.dragging-ew * {
  cursor: ew-resize !important;
  user-select: none !important;
}

.mono-active > div:not(.not-mono) > span {
  color: theme('colors.gray.600');
}

.mono > div > span {
  transition-duration: 0.5s;
  transition-property: background-color, border-color, color, fill, stroke;
}

.changing-theme,
.changing-theme * {
  transition: none !important;
}

input[type='checkbox'] {
  margin: 0 !important;
}

.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.fade-down-light {
  background-image: linear-gradient(
    0deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 1) 30%,
    rgba(255, 255, 255, 1) 100%
  );
}

.fade-down-dark {
  background-image: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 1) 30%,
    rgba(0, 0, 0, 1) 100%
  );
}
