@import 'tailwindcss/components';

@layer components {
  .prose {
    @apply dark:text-inherit;
  }

  .prose thead {
    @apply dark:border-gray-700/50;
  }

  .prose tr {
    @apply dark:border-gray-800/50;
  }

  .prose hr {
    @apply dark:border-gray-800/50;
  }

  .prose strong {
    @apply dark:text-gray-50;
  }

  .prose a {
    @apply dark:text-gray-50;
  }

  .prose h3 {
    @apply dark:text-gray-50;
  }

  .prose blockquote {
    @apply dark:text-gray-400 dark:border-gray-800;
  }

  .prose ul > li::before {
    @apply bg-gray-600 dark:bg-gray-400;
  }

  .prose-sm ul {
    @apply pl-0;
  }
}

/* Following styles are for ZoomImage component */
/* Based on 'react-medium-image-zoom' package css */
[data-rmiz] {
  position: relative;
  overflow: hidden;
}

[data-rmiz-ghost] {
  position: absolute;
  pointer-events: none;
}

[data-rmiz-btn-zoom],
[data-rmiz-btn-unzoom] {
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 50%;
  border: none;
  box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
  color: #fff;
  height: 40px;
  margin: 0;
  outline-offset: 2px;
  padding: 9px;
  touch-action: manipulation;
  width: 40px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

[data-rmiz-btn-zoom]:not(:focus):not(:active) {
  position: absolute;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  pointer-events: none;
  white-space: nowrap;
  width: 1px;
}

[data-rmiz-btn-zoom] {
  position: absolute;
  inset: 10px 10px auto auto;
  cursor: zoom-in;
}

[data-rmiz-btn-unzoom] {
  display: none;
}

[data-rmiz-content='found'] img,
[data-rmiz-content='found'] svg,
[data-rmiz-content='found'] [role='img'],
[data-rmiz-content='found'] [data-zoom] {
  cursor: zoom-in;
}

a [data-rmiz-content='found'] img,
a [data-rmiz-content='found'] svg,
a [data-rmiz-content='found'] [role='img'],
a [data-rmiz-content='found'] [data-zoom] {
  cursor: pointer;
}

[data-rmiz-modal]::backdrop {
  display: none;
}

[data-rmiz-modal][open] {
  position: fixed;
  width: 100vw;
  width: 100svw;
  height: 100vh;
  height: 100svh;
  max-width: none;
  max-height: none;
  margin: 0;
  padding: 0;
  border: 0;
  background: transparent;
  overflow: hidden;
}

[data-rmiz-modal-overlay] {
  position: absolute;
  inset: 0;
  transition: background-color 0.3s;
}

[data-rmiz-modal-overlay='hidden'] {
  background-color: rgba(0, 0, 0, 0);
}

[data-rmiz-modal-overlay='visible'] {
  background-color: rgba(0, 0, 0);
}

[data-rmiz-modal-content] {
  position: relative;
  width: 100%;
  height: 100%;
}

[data-rmiz-modal-img] {
  position: absolute;
  cursor: zoom-out;
  image-rendering: high-quality;
  transform-origin: top left;
  transition: transform 0.3s;
}

@media (prefers-reduced-motion: reduce) {
  [data-rmiz-modal-overlay],
  [data-rmiz-modal-img] {
    transition-duration: 0.01ms !important;
  }
}
