@tailwind base;

@layer base {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    @apply font-headingsFamily;
  }

  body {
    @apply font-bodyFamily;
  }
}

.dark {
  color-scheme: dark;
}

html {
  height: 100%;
  font-feature-settings: 'cv02', 'cv03', 'cv04', 'cv11';
  overflow-y: overlay; /* Shows scrollbar over content instead of causing a layout shift */
  @apply [--scroll-mt:9.5rem] lg:[--scroll-mt:6.3rem];
  @apply overscroll-y-none;
}

body {
  min-height: 100%;
}

abbr[title] {
  text-decoration: none;
}

select::-ms-expand {
  display: none;
}

select {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: '';
}

/* Always allocates space for the scrollbar even if it is not currently overflowing */
.stable-scrollbar-gutter {
  scrollbar-gutter: 'stable';
}

[id^='user-content-fn-'] {
  display: none;
}

/* Display icons inline if it belongs to a piece of text */
ul > li .icon {
  display: inline;
}
p .icon {
  display: inline;
}
span .icon {
  display: inline;
}

.update-container {
  scroll-margin-top: calc(var(--scroll-mt) - 2rem);
}

.param-head {
  scroll-margin-top: calc(var(--scroll-mt) - 1.25rem);
}

.tab-container {
  scroll-margin-top: calc(var(--scroll-mt) - 1.25rem);
}

.katex {
  position: relative;
}

@media print {
  .codeblock-light,
  .codeblock-dark {
    break-inside: avoid;
    page-break-inside: avoid;
  }
}
