/* Lines of code outside code blocks. Generated when you use single ticks: `example` */
:not(pre) > code {
  @apply break-words rounded-md px-2 py-0.5 text-gray-600 dark:text-gray-200 dark:border-gray-800 bg-gray-100/50 dark:bg-white/5;
}

:not(pre) > code:before,
:not(pre) > code:after {
  content: none !important;
}

/* Used for API response */
/* Auto mode */
span.language-json-auto .token.punctuation {
  @apply text-gray-600 dark:text-gray-300;
}

span.language-json-auto .token.property {
  @apply text-sky-600 dark:text-[#9cdcfe];
}

span.language-json-auto .token.string {
  @apply text-amber-600 dark:text-[#ce9178];
}

span.language-json-auto .token.number {
  @apply text-green-600 dark:text-[#b5cea8];
}

/* Dark mode */
span.language-json-dark .token.punctuation {
  @apply text-gray-300;
}

span.language-json-dark .token.property {
  @apply text-[#9cdcfe];
}

span.language-json-dark .token.string {
  @apply text-[#ce9178];
}

span.language-json-dark .token.number {
  @apply text-[#b5cea8];
}
