.latex-style {
  font-family: Latin Modern;
  line-height: 1.8;
  max-width: 80ch;
  margin: 0 auto;
  padding: 2rem 1.25rem;
  counter-reset: theorem definition sidenote-counter lemma;
  color: hsl(0, 5%, 10%);
}

.latex-style .theorem {
  counter-increment: theorem;
  display: block;
  margin: 12px 0;
  font-style: italic;
}

.latex-style .theorem::before {
  content: 'Theorem ' counter(theorem) '. ';
  font-weight: bold;
  font-style: normal;
}

.latex-style .theorem p {
  display: unset;
}

.latex-style .lemma {
  counter-increment: lemma;
  display: block;
  margin: 12px 0;
  font-style: italic;
}

.latex-style .lemma::before {
  content: 'Lemma ' counter(lemma) '. ';
  font-weight: bold;
  font-style: normal;
}

.latex-style .lemma p {
  display: unset;
}

.latex-style .proof {
  display: block;
  margin: 12px 0;
  font-style: normal;
  position: relative;
}

.latex-style .proof::before {
  content: 'Proof.' attr(title);
  font-style: italic;
}

.latex-style .proof::after {
  content: ;
  position: absolute;
  right: -12px;
  bottom: -2px;
}

.latex-style .proof p {
  display: unset;
}

.latex-style .definition {
  counter-increment: definition;
  display: block;
  margin: 12px 0;
  font-style: normal;
}

.latex-style .definition::before {
  content: 'Definition ' counter(definition) '. ';
  font-weight: bold;
  font-style: normal;
}

.latex-style .definition p {
  display: unset;
}

.latex-style .author {
  margin: 0.85rem 0;
  font-variant-caps: small-caps;
  text-align: center;
}

.latex-style .footnotes {
  border-top: 1px solid hsl(0, 0%, 39%);
}

.latex-style .footnotes p {
  text-align: left;
  line-height: 1.5;
  font-size: 85%;
  margin-bottom: 0.4rem;
}

.latex-style .abstract {
  text-align: center;
  margin-top: 0;
  margin: 2.25rem 0;
}

.latex-style .abstract p {
  text-align: center;
  margin-top: 0;
}

.latex-style .abstract > h2 {
  font-size: 1rem;
  margin-bottom: -0.2rem;
}

.latex-style .latex span {
  text-transform: uppercase;
  line-height: 1ex;
}

.latex-style .latex span:nth-child(1) {
  font-size: 0.75em;
  vertical-align: 0.28em;
  margin-left: -0.48em;
  margin-right: -0.15em;
}

.latex-style .latex span:nth-child(2) {
  vertical-align: -0.5ex;
  margin-left: -0.1667em;
  margin-right: -0.125em;
}

